.screen {
  height: 60px;
  width: 100%;
  margin-bottom: 10px;
  padding: 0 10px;
  background-color: var(--greyLight-1);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-family: "Comfortaa", cursive;
  font-size: var(--font-normal-2);
  box-sizing: border-box;
  box-shadow: var(--inner-shadow);
  background: none;
}
