.container {
  width: 90%;
  min-width: var(--min-width);
  margin: 15px auto 0;
  padding: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #fff;
}

.title {
  margin: 15px 0;
  font-family: "Comfortaa", cursive;
  font-weight: 700;
  font-size: var(--font-big);
  text-transform: uppercase;
  transition: var(--transition);
  user-select: none;
}
